import { useEffect, useState } from "react";
import axios from "axios";
import { gapi } from "gapi-script";

const CLIENT_ID = "YOUR_GOOGLE_CLIENT_ID";
const API_KEY = "YOUR_GOOGLE_API_KEY";
const SCOPES = "https://www.googleapis.com/auth/gmail.readonly";

const EmailPhishing = () => {
  const [emails, setEmails] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    const initClient = () => {
      gapi.load("client:auth2", () => {
        gapi.client.init({
          apiKey: API_KEY,
          clientId: CLIENT_ID,
          scope: SCOPES,
          discoveryDocs: [
            "https://www.googleapis.com/discovery/v1/apis/gmail/v1/rest",
          ],
        });
      });
    };
    initClient();
  }, []);

  // Function to analyze emails for phishing
  const analyzeEmails = async (emailText) => {
    try {
      const response = await axios.post(
        "http://127.0.0.1:5000/predict-email",
        { email: emailText },
        { headers: { "Content-Type": "application/json" } }
      );
      return response.data.result; // Returns "Phishing Email" or "Safe Email"
    } catch (err) {
      console.error("Error analyzing email:", err);
      return "Analysis Failed";
    }
  };

  const signInAndFetchEmails = async () => {
    setLoading(true);
    try {
      const GoogleAuth = gapi.auth2.getAuthInstance();
      await GoogleAuth.signIn(); // User logs in
      const token = GoogleAuth.currentUser.get().getAuthResponse().access_token;

      // Fetch latest emails
      const response = await axios.get(
        "https://www.googleapis.com/gmail/v1/users/me/messages",
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );

      const messages = response.data.messages.slice(0, 5); // Get latest 5 emails

      // Fetch and analyze each email
      const emailDetails = await Promise.all(
        messages.map(async (msg) => {
          const emailData = await axios.get(
            `https://www.googleapis.com/gmail/v1/users/me/messages/${msg.id}`,
            {
              headers: { Authorization: `Bearer ${token}` },
            }
          );
          const emailText = emailData.data.snippet;
          const result = await analyzeEmails(emailText);
          return { text: emailText, result };
        })
      );

      setEmails(emailDetails);
    } catch (err) {
      console.error("Error fetching emails:", err);
      setError("Error fetching emails.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <h2>Email Phishing Detector</h2>
      <button onClick={signInAndFetchEmails} disabled={loading}>
        {loading ? "Fetching Emails..." : "Fetch Gmail Emails"}
      </button>
      {error && <p style={{ color: "red" }}>{error}</p>}
      <ul>
        {emails.map((email, index) => (
          <li key={index}>
            <strong>{email.result}</strong>: {email.text}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default EmailPhishing;
