import { useState } from "react";
import axios from "axios";
import "../App.css";
const URLPhishing = () => {
  const [url, setUrl] = useState("");
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [websiteAnalysis, setWebsiteAnalysis] = useState(null); // New state for website analysis

  const checkURL = async () => {
    if (!url.trim()) {
      setResult(null);
      setError("Please enter a valid URL.");
      return;
    }

    setLoading(true);
    setResult(null);
    setError(null);
    setWebsiteAnalysis(null); // Clear website analysis on new request

    try {
      const response = await axios.post(
        "http://127.0.0.1:5000/predict-url",
        { URL: url },
        { headers: { "Content-Type": "application/json" } }
      );
      setResult(response.data.result);
      setWebsiteAnalysis(response.data.website_analysis); // Set website analysis
    } catch (err) {
      console.error("Error checking URL:", err);
      setError("Error checking URL. Try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <h1>URL Phishing Detection</h1>
      <input 
        type="text"
        value={url}
        onChange={(e) => setUrl(e.target.value)}
        placeholder="Enter URL"
      />
      <button onClick={checkURL} disabled={loading}>
        {loading ? "Checking..." : "Check URL"}
      </button>
      {error && <p style={{ color: "red" }}>{error}</p>}
      {result && <p>Result: {result}</p>}
      {websiteAnalysis && <p>Website Analysis: {websiteAnalysis}</p>} {/* Display website analysis */}
    </div>
  );
};

export default URLPhishing;